.modal__wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    z-index: 100;
    width: 100%;
    height: 100%;
    top: 0rem;
    left: 0rem;
}
.modal {
    display: flex;
    flex-direction: column;
    max-width: 720px;
    margin: auto;
    position: absolute;
    z-index: 100;
    background: #1C1C21;
    border: 1px solid rgba(76, 76, 255, 0.2);
    /* box-sizing: border-box; */
    box-shadow: 0px 24px 32px rgba(0, 0, 0, 0.04), 0px 16px 24px rgba(0, 0, 0, 0.04), 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 0px 1px rgba(0, 0, 0, 0.04);
    border-radius: 40px;
}

.modal__header {
    display: flex;
    justify-content: space-between;
}

.modal__header svg {
    margin-left: auto;
}

.modal__card {
    display: flex;
    justify-content: center;
    flex-direction: column;
}